<template>

  <div>
    <my-nav-bar>
      <!--<span class="text-color-fe form-nav">家族详情</span>-->
      <search-input @keyword="handleKeyWord"></search-input>
    </my-nav-bar>

    <div class="app-family-main" @touchmove="touchMove($event)" @touchend="touchEnd($event)">

      <ul class="app-family-list-box js_fl_list">

        <!--<li v-for="item in list" :key="item.id">{{item.id}}</li>-->
        <family-list v-for="item in list" :key="item.id" v-bind:datalist="item"></family-list>

      </ul>
      <my-scroll v-bind="scrllObj"></my-scroll>

    </div>
    <transition name="fade">
      <info v-bind:infotext="infoshow.infotext" v-if="infoshow.isshow"></info>
    </transition>
    
  </div>


</template>

<script>
  import myNavBar from './myNavBar';
  import SearchInput from './SearchInput';
  import FamilyList from './FamilyList';
  import myScroll from '../components/my-scroll/scroll';
  import info from './info';

  export default {
    data() {
      return {
        list: [],
        server: "http://139.199.23.160:8080/qmzb",
        page: 1, //页码
        keyword: '',
        isEnd: false, //是否到底
        isLoading: true, //上拉是否可以刷新了 
        infoshow:{
           isshow:false,
          infotext:""
        }
      }
    },
    beforeCreate: function () {
      //实例创建之前
      //加载数据
      // let form_2 = new FormData();
      // let vw = this
      // let server = "http://139.199.23.160:8080/qmzb"
      // form_2.append("keyword", "");
      // form_2.append("page", 1);
      // this.$http.post(server + "/check/getFamilyList", form_2).then(function (response) {
      //   //成功

      //   this.list = this.list.concat(response.data.data.list);



      // }.bind(this)).catch(function (erro) {
      //   //失败
      // });

    },
    computed: {
      "scrllObj": function () {
        return {
          "init": false,
          "isend": false,
          "isloading": false,
          "istab": false
        }
      }
     
    },

    created: function () {
      //实例创建后执行
      this.dataLoad();



    },
    components: {
      myNavBar,
      SearchInput,
      FamilyList,
      myScroll,
      info

    },
    methods: {
      dataLoad: function () {
        //加载数据
        let form_2 = new FormData();
        let vw = this;
        form_2.append("keyword", "");
        form_2.append("page", vw.page);
        this.$http.post(vw.server + "/check/getFamilyList", form_2).then(function (response) {
          //成功

          if (response.data.code == 200) {
            vw.list = vw.list.concat(response.data.data.list);

            vw.isEnd = vw.page == response.data.data.allpages;

            if (vw.isEnd) {
              //没有更多了
              //显示文字
              vw.scrllObj.isend = true;
              if (response.data.data.allpages == 1) {
                vw.scrllObj.isend = false;
                vw.scrllObj.init = false;
              }

            } else {
              //还有下一页
              vw.page++;
              vw.scrllObj.isend = false;
              vw.scrllObj.init = true;

            }
          } else {
            //失败
            alert("加载失败");
          }


        }.bind(this)).catch(function (erro) {
          //失败
        });

      },
      handleKeyWord: function (msg) {
        let form_2 = new FormData();
        this.keyword = msg;
        let vw = this;
      
        form_2.append("keyword", msg);
        form_2.append("page", 1);
        this.$http.post(vw.server + "/check/getFamilyList", form_2).then(function (response) {
          //成功
          if (response.data.data.list.length > 0) {
                this.list = response.data.data.list;
                //console.log(this.list);
                this.infoshow.isshow=false;
              
          } else {
            //没有结果 提示没有您想要的结果
            this.ShowInfo("没有您想要的结果");
            //  this.infoshow.isshow=true;

            //   this.infoshow.infotext="没有您想要的结果";
          
              
          }
          


        }.bind(this)).catch(function (erro) {
          //失败
        });



      },
      touchMove(e) {
        //移动时
        var screenhight = window.screen.height;
        let vw = this;
        window.onscroll = function () {
          if (!vw.isEnd) {
            if (screenhight + document.body.scrollTop > parseFloat(document.body.scrollHeight) + 100) {
            
                vw.isLoading = true;
                vw.scrllObj.istab = true;
                vw.scrllObj.init = false;
              
           
            } else {
              vw.scrllObj.istab = false;
              vw.scrllObj.init = true;
              vw.isLoading = false;
            }
          } else {
               vw.scrllObj.isend = true;
          }
        }

      },
      touchEnd(e) {
        //手指抬起
        if (this.isLoading & !this.isEnd) {
          //加载下一页
            vw.dataLoad();
             vw.scrllObj.istab = false;
              vw.scrllObj.init = false;
             vw.scrllObj.isloading = true;
          this.isLoading = false;
        }

      },
      ShowInfo(msg){
          this.infoshow.isshow=true;

          this.infoshow.infotext=msg;
          let vw=this;
        
          setTimeout(function () {  
               vw.infoshow.isshow=false;
          },2000);
      }


    }

  }

</script>

<style>
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
  opacity: 0
}

</style>



