<template lang="html">
  <div id="pullUp" class="container">
      <span class="pullUpIcon" v-if="init"></span>
    <span class="pullUpIcon pullUpIcon-down" v-if="istab"></span>
    <span class="pullUpIcon  pullUpIcon-loading"  v-if="isloading"></span>
    <span class="pullUpLabel"  v-if="init">上拉加载更多...</span>
     <span class="pullUpLabel"   v-if="isloading">加载中...</span>
      <span class="pullUpLabel"  v-if="isend">没有更多啦</span>

  </div>
</template>

<script>
  export default {  
      props:["init","isloading","istab","isend"]
      
  }

</script>
<style>
 /*上拉加载更多*/
#pullUp {
    height: 65px;
    padding: 10px 10px 0px 0px;
}

#pullDown, #pullUp {
    background: transparent;
    height: 45px;
    line-height: 40px;
    padding-top: 20px;

    font-weight: bold;
    font-size: 14px;
    color: #888;
    margin-top: -15px;
      /*display: none;*/
 
}

 #pullUp .pullUpIcon {
    display: block;
    float: left;
    width: 40px;
    height: 40px;
    background: url("../../assets/pull-icon@2x.png") 0 0 no-repeat;
    /*background-position-y: -40px;*/
    -webkit-background-size: 40px 80px;
    background-size: 40px 80px;
    -webkit-transition-property: -webkit-transform;
    -webkit-transition-duration: 250ms;
      -moz-transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
     -moz-transform: rotate(180deg);
   
   
    }
    #pullUp .pullUpIcon-down{
          -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
     -moz-transform: rotate(0deg);
    }
    #pullUp .pullUpIcon-loading{
         background: url("../../assets/pull-icon@2x.png") 0 0 no-repeat;
         -webkit-background-size: 40px 80px;
         background-size: 40px 80px;
        background-position-y: -40px;
        display: none;
    }


</style>



