<template>
  <!--上面导航栏-->
  <div class="nav">
    <div class="container nav-bar">
      <a href="javascript:;" onclick="history.back()" class="form-nav"> &lt; </a>

      <slot></slot>

    </div>
  </div>


</template>
<script>
  export default {
    name: 'myNavBar',
    data() {
      return {

      }
    },

  }

</script>
<style scoped>
  .container {
    width: 94vw;
    margin: 0 auto;
  }
  a{
    text-decoration: none;
  }
  .nav {
    width: 100%;
    background: #fff;
    box-shadow: 0 0 1px #333;
  }

  .nav-bar {
    position: relative;
    display: flex;
    display: -webkit-box;
    /* 老版本语法: Safari, iOS, Android browser, older WebKit browsers. */
    display: -moz-box;
    /* 老版本语法: Firefox (buggy) */
    display: -ms-flexbox;
    /* 混合版本语法: IE 10 */
    display: -webkit-flex;
    /* 新版本语法: Chrome 21+ */
    justify-content: center;
    -webkit-box-pack: center;
    -moz-justify-content: center;
    -webkit-justify-content: center;
    align-items: center;
    -webkit-box-align: center;
    -moz-align-items: center;
    -webkit-align-items: center;
    height: .44rem;
    font-size: .18rem;
  }

  .text-color-fe {
    color: #fe426f;
  }

  .nav-bar>a {
    position: absolute;

    font-size: .26rem;
    color: #aeaeae;
    display: flex;
    display: -webkit-box;
    /* 老版本语法: Safari, iOS, Android browser, older WebKit browsers. */
    display: -moz-box;
    /* 老版本语法: Firefox (buggy) */
    display: -ms-flexbox;
    /* 混合版本语法: IE 10 */
    display: -webkit-flex;
    /* 新版本语法: Chrome 21+ */
    align-items: center;
    -webkit-box-align: center;
    -moz-align-items: center;
    -webkit-align-items: center;
  }

  .nav-bar>a:first-child {
    left: 0;
  }

</style>



