<template>

  <div class="modal-info" >
    <div class="modal-content">
      <div class="other-modal-info">
        <p class="info-text">
          {{infotext_2}}
        </p>

      </div>

    </div>
    <div class="close-modal">

    </div>
  </div>

</template>

<script>
  export default {

    props: ["isshow", "infotext"],
    data() {
      return {
        isshow_2: this.isshow,
        infotext_2: this.infotext
      }
    },
    created: function () {
      //
      //console.log(this.isshow_2)
    },
    computed:{

    },
    watch: {
      isshow_2: "ShowInfo"
    },
    methods: {
      ShowInfo() {
        let vw = this;
        //console.log(this.isshow_2)
            alert("true");
      }
    }
  }

</script>

<style>
  /*模态框*/

  .modal-info {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0);

    z-index: 1000;
  }

  .close-modal {
    position: absolute;
    top: 0;
    left: 0;
    top: 0;
    right: 0;

    z-index: 1;
  }

  .modal-content {
    background: rgba(6, 6, 6, .6);
    color: #fff;
    width: 70vw;
    position: fixed;
    bottom: 50%;
    left: 50%;
    margin-left: -35vw;
    border-radius: .04rem;
    z-index: 110;
  }

  .modal-content>div.other-modal-info {
    display: flex;
    display: -webkit-box;
    /* 老版本语法: Safari, iOS, Android browser, older WebKit browsers. */
    display: -moz-box;
    /* 老版本语法: Firefox (buggy) */
    display: -ms-flexbox;
    /* 混合版本语法: IE 10 */
    display: -webkit-flex;
    /* 新版本语法: Chrome 21+ */
    justify-content: center;
    -webkit-box-pack: center;
    -moz-justify-content: center;
    -webkit-justify-content: center;
    flex-direction: column;
    /*display: none;*/
  }

  .modal-content>div>p {
    display: flex;
    display: -webkit-box;
    /* 老版本语法: Safari, iOS, Android browser, older WebKit browsers. */
    display: -moz-box;
    /* 老版本语法: Firefox (buggy) */
    display: -ms-flexbox;
    /* 混合版本语法: IE 10 */
    display: -webkit-flex;
    /* 新版本语法: Chrome 21+ */
    height: .45rem;
    justify-content: center;
    -webkit-box-pack: center;
    -moz-justify-content: center;
    -webkit-justify-content: center;
    align-items: center;
    -webkit-box-align: center;
    -moz-align-items: center;
    -webkit-align-items: center;
    border-bottom: 1px solid #e7e7e7;
    cursor: pointer;
  }

  .modal-content>div>p:last-child {
    border-bottom: 0;
  }

  .modal-info p a {
    display: block;
    width: 100%;
    height: 100%;
    display: flex;
    display: -webkit-box;
    /* 老版本语法: Safari, iOS, Android browser, older WebKit browsers. */
    display: -moz-box;
    /* 老版本语法: Firefox (buggy) */
    display: -ms-flexbox;
    /* 混合版本语法: IE 10 */
    display: -webkit-flex;
    /* 新版本语法: Chrome 21+ */
    justify-content: center;
    -webkit-box-pack: center;
    -moz-justify-content: center;
    -webkit-justify-content: center;
    align-items: center;
    -webkit-box-align: center;
    -moz-align-items: center;
    -webkit-align-items: center;
    color: #fe426f;
  }
  /*模态框*/

</style>



