<template>

        
       <li >  
            <router-link :to="{path:'/family', name:'family-detail',params:{'id':datalist.id}}">
       
      <div class="container app-family-list-con clearfloat">
        <div class="app-family-left">
          <i class="app-family-icon app-family-icon-1"   v-bind:class="{'app-family-icon-1':datalist.id==1 }"></i>
          <img :src="datalist.logo" alt="">
        </div>
        <div class="app-family-mid">
          <p>{{datalist.familyName}}</p>
          <span>ID {{datalist.id}}</span>
        </div>
        <div class="app-family-right">
          家族收益: <span>{{datalist.monthTotalTicket}}民票</span>
        </div>

      </div>
    
    </router-link>
         </li>  


</template>

<script>
  export default {
    data() {
      return {
        id:""
      }
    },
    beforeCreate:function(){
        //创建之前 请求数据
    },
    props:["datalist"],
    methods: {
        handleID:function(){
         
        }
    }
  }

</script>

<style>
  .container {
    width: 94vw;
    margin: 0 auto;
  }

.app-family-list-box li{
    width: 100%;
    height: .9rem;
      color: rgb(174,174,174);
    background: #fff;
    margin-bottom: 2px;
    list-style: none;

}
.app-family-list-box li a{
    display: block;
    width: 100%;
    height: 100%;
     color: rgb(174,174,174);
}
.app-family-left{
    width: 44px;
    padding-top: 11px;
    text-align: center;
     display: flex;
    display: -webkit-box;
    /* 老版本语法: Safari, iOS, Android browser, older WebKit browsers. */
    display: -moz-box;
    /* 老版本语法: Firefox (buggy) */
    display: -ms-flexbox;
    /* 混合版本语法: IE 10 */
    display: -webkit-flex;
    /* 新版本语法: Chrome 21+ */
    align-items: center;
    -webkit-box-align: center;
    -moz-align-items: center;
    -webkit-align-items: center;
    flex-direction: column;
}
.app-family-icon{
    display:block;
    width: 24px;
    height: 16px;
   
}
.app-family-list-con{
    position: relative;
}
.app-family-list-con>div{
    float: left;
}
.app-family-icon-1{
    /*background: url("../img/family_icon_top1.png") no-repeat;*/
    background-size: 100% 100%;
}
.app-family-icon-2{
    /*background: url("../img/family_icon_top2.png") no-repeat;*/
    background-size: 100% 100%;
}
.app-family-icon-3{
    /*background: url("../img/family_icon_top3.png") no-repeat;*/
    background-size: 100% 100%;
}
.app-family-left img{
    display: block;
    width: 44px;
    height: 44px;
    border-radius: 50%;
    border: 1px solid #ccc;
}

.app-family-mid{
    
    height: 100%;
    line-height: 0;
    margin-left: .1rem;
    padding-top: .35rem;
}
.app-family-mid p{
    width: 100%;
    font-size: .16rem;
    color: rgb(64,64,64);
    margin-bottom: .24rem;
}
.app-family-right{
    line-height: 0;
    position: absolute;
    top: .59rem ;
    right: 0;
}


</style>



