<template>
     <div class="search-input-box">
                <i class="form-nav search-box-i"></i>
                <input type="text" class="family-search js_family-_search"  @keyup="handleChange" v-model='keyword' placeholder="请输入家族ID/名称">
            </div>
</template>

<script>
    export default {
        name:'SearchInput',
        data(){
            return{
                keyword:''
            }
        },
        methods:{
            
            handleChange:function(){
                this.$emit("keyword",this.keyword);
              
            }
        }

    }
</script>
<style >
    .search-input-box{
    position: absolute;
    height: 28px;
    width: 100%;
    padding-left: .54rem;
    right: 0;
}
.search-input-box input{
    width: 100%;
    height: 100%;
    color: rgb(174,174,174);
    border: 1px solid #aeaeae;
    border-radius: 40px;
    padding-left:20px; 
    font-size: .14rem;
    outline: none;
    background: #fff;
     -webkit-appearance: none;

}
input{
     -webkit-appearance: none;
}

.search-box-i{
    position: absolute;
   
    font-size: .26rem;
    color: #aeaeae;
    display: flex;
    display: -webkit-box;
    /* 老版本语法: Safari, iOS, Android browser, older WebKit browsers. */
    display: -moz-box;
    /* 老版本语法: Firefox (buggy) */
    display: -ms-flexbox;
    /* 混合版本语法: IE 10 */
    display: -webkit-flex;
    /* 新版本语法: Chrome 21+ */
    align-items: center;
    -webkit-box-align: center;
    -moz-align-items: center;
    -webkit-align-items: center;
      width: 14px;
     height: 14px;
     background: url("../assets/search_icon_search@2x.png") no-repeat;
     background-size: 100% 100%;
     top: 7px;
     left: .6rem;
}


</style>



