<template>

  <div>
    <my-nav-bar>

      <span class="text-color-fe form-nav">家族详情</span>
    </my-nav-bar>
    <banner></banner>
    <div class="app-family-main app-family-detail-main" style="margin-top:0;">

      <ul class="app-family-list-box">
        <li>

          <div class="container app-family-list-con clearfloat">
            <div class="app-family-left">
              <i class="app-family-icon app-family-icon-1"></i>
              <img :src="datalist.logo" alt="">
            </div>

            <div class="app-family-mid ">
              <p class="js_family_name">{{datalist.familyName}}</p>
              <span class="js_family_id">ID {{datalist.id}}</span>
            </div>
            <div class="app-family-right">
              家族收益: <span class="js_family_income">{{datalist.monthTotalTicket}}民票</span>
            </div>

          </div>

        </li>

      </ul>
      <div class="family-row">
        <div class="container">
          <span class="family-row-title">家族公告</span>
          <div class="family-row-content js_family_notice" >
              {{datalist.notic}}  
          </div>
          <p><a href="#" @click="NoticehandleLuanch($event)" class="js_getmore" >{{noticebuttonText}}</a></p>
        </div>
      </div>
      <div class="family-row">
        <div class="container">
          <span class="family-row-title">家族介绍</span>
          <div class="family-row-content js_family_det">
              {{datalist.declaration}}
          </div>
          <p><a href="#" class="js_getmore"  @click="DeclarationhandleLuanch($event)">{{declarationbuttonText}}</a></p>
        </div>
      </div>
      <div class="family-apply-box">
        <a href="#" @click="handleSubmit($event)" class="js_apply_btn">申请加入</a>
      </div>

    </div>

  </div>

</template>

<script scoped>
  import Banner from './Banner';
  import myNavBar from './myNavBar';
//   import moduleName from 'module';
  export default {
    data() {
      return {
        id: "",
       datalist:{},
       actived:false,
       noticebuttonText:"查看更多",
        declarationbuttonText:"查看更多"
      }
    },
    computed:{
     
    },
    ready: function () {

    },
    beforeCreate: function () {
      //创建之前 请求数据



    },
    created: function () {
      this.id = this.$route.params.id;
      if (this.id) {
        this.detailDataLoad();
       
      }

    },
    methods: {
      getParams() {
        // 取到路由带过来的参数 
         this.id = this.$route.params.id;
        // 将数据放在当前组件的数据内

        if ( this.id) {
          this.detailDataLoad();
        }

      },
      detailDataLoad: function () {
        //数据   let form_2 = new FormData();
        let form_2 = new FormData();
        let server = "http://139.199.23.160:8080/qmzb"
        let id = this.id;
        form_2.append("id", id);
        form_2.append("page", 1);
        this.$http.post(server + "/check/getFamilyList", form_2).then(function (response) {
          //成功
          this.datalist = response.data.data.list[0];
      
        }.bind(this)).catch(function (erro) {
          //失败
        });

      },
      NoticehandleLuanch:function(e){
        e.preventDefault();
        //点击展开

      },
      DeclarationhandleLuanch:function(e){
         e.preventDefault();

        //点击展开

      },
      handleSubmit:function (e) {  
        //提交
        e.preventDefault();

      
    }


    },
    components: {
      Banner,
      myNavBar
    },
    watch: {
      "$route": "getParams"
    }
  }

</script>

<style>
  .container {
    width: 94vw;
    margin: 0 auto;
  }

  .app-family-list-box li {
    width: 100%;
    height: .9rem;
    color: rgb(174, 174, 174);
    background: #fff;
    margin-bottom: 2px;
    list-style: none;
  }

  .app-family-list-box li a {
    display: block;
    width: 100%;
    height: 100%;
    color: rgb(174, 174, 174);
  }

  .app-family-left {
    width: 44px;
    padding-top: 11px;
    text-align: center;
    display: flex;
    display: -webkit-box;
    /* 老版本语法: Safari, iOS, Android browser, older WebKit browsers. */
    display: -moz-box;
    /* 老版本语法: Firefox (buggy) */
    display: -ms-flexbox;
    /* 混合版本语法: IE 10 */
    display: -webkit-flex;
    /* 新版本语法: Chrome 21+ */
    align-items: center;
    -webkit-box-align: center;
    -moz-align-items: center;
    -webkit-align-items: center;
    flex-direction: column;
  }

  .app-family-icon {
    display: block;
    width: 24px;
    height: 16px;
  }

  .app-family-list-con {
    position: relative;
  }

  .app-family-list-con>div {
    float: left;
  }

  .app-family-icon-1 {
    /*background: url("../img/family_icon_top1.png") no-repeat;*/
    background-size: 100% 100%;
  }

  .app-family-icon-2 {
    /*background: url("../img/family_icon_top2.png") no-repeat;*/
    background-size: 100% 100%;
  }

  .app-family-icon-3 {
    /*background: url("../img/family_icon_top3.png") no-repeat;*/
    background-size: 100% 100%;
  }

  .app-family-left img {
    display: block;
    width: 44px;
    height: 44px;
    border-radius: 50%;
    border: 1px solid #ccc;
  }

  .app-family-mid {

    height: 100%;
    line-height: 0;
    margin-left: .1rem;
    padding-top: .35rem;
  }

  .app-family-mid p {
    width: 100%;
    font-size: .16rem;
    color: rgb(64, 64, 64);
    margin-bottom: .24rem;
  }

  .app-family-right {
    line-height: 0;
    position: absolute;
    top: .59rem;
    right: 0;
  }


  .app-family-detail-main ul {
    margin-bottom: .07rem;
  }

  .family-row {
    background: #fff;
    color: rgb(174, 174, 174);
    margin-bottom: .07rem;
    padding-top: .13rem;
  }

  .family-row .family-row-title {
    display: block;
    font-size: .18rem;
    color: rgb(64, 64, 64);
    margin-bottom: .1rem;
  }

  .family-row-content {
    line-height: .2rem;
    min-height: 1.2rem;
    height: 1.2rem;
    overflow: hidden;

    text-overflow: ellipsis;
    white-space: wrap;
  }

  .family-row-content.active {
    height: auto;
  }

  .family-row>div>p {
    height: .4rem;
    line-height: .4rem;
    font-size: .16rem;

    text-align: center;
  }

  .family-row p a {
    color: rgb(174, 174, 174);
  }

  .family-apply-box {
    padding: .2rem 0;
    text-align: center;
    padding-bottom: .4rem;
  }

  .family-apply-box a {
    display: inline-block;
    width: 2.29rem;
    height: .43rem;
    color: #fff;
    background: #fe426f;
    font-size: .16rem;
    text-align: center;
    line-height: .43rem;
    border-radius: .05rem;
  }

</style>



